"use client";

import { useAuthContext } from "../context/AuthContext";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

export default function Dashboard() {
  const { broker, logout, isAuthenticated } = useAuthContext();
  const [stats, setStats] = useState({
    totalLeads: 10,
    contactedLeads: 5,
    upcomingLeads: 3,
    packages: 2
  });
const router = useRouter();

  if (!isAuthenticated) return(<> 
  <div className="flex flex-col items-center justify-center min-h-screen bg-background">
  <p className="text-black">Please login first.</p>
  <div>
 
   <button 
   onClick={()=>{
    router.push('/broker-login');
   }}
   className="ml-4 bg-blue-500 text-white px-4 py-2 rounded"
   >Login</button>

    <button
    onClick={()=>{
      router.push('/broker-signup');
    }}
    className="ml-4 bg-green-500 text-white px-4 py-2 rounded"
    >Signup</button>

  </div>

  </div>
  </>)

  return (
    <div className="p-8 text-black bg-background min-h-screen">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold">Welcome, {broker?.name}</h1>
        <button onClick={logout} className="bg-red-500 text-white px-4 py-2 rounded">Logout</button>
      </div>
      <div className="grid grid-cols-2 gap-4">
        <div className="p-4 border rounded">Total Leads: {stats.totalLeads}</div>
        <div className="p-4 border rounded">Contacted Leads: {stats.contactedLeads}</div>
        <div className="p-4 border rounded">Upcoming Leads: {stats.upcomingLeads}</div>
        <div className="p-4 border rounded">Packages: {stats.packages}</div>
      </div>
    </div>
  );
}
